/* Main navbar style */
.navbar {
  background-color: #1f2937; 
  color: white;
  padding: 1rem;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.25rem; 
  font-weight: bold;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 1rem; 
}

.dark-toggle {
  margin-left: 1rem; 
  padding: 0.5rem;
  background-color: #374151; 
  border: none;
  border-radius: 0.375rem;
  cursor: pointer;
  color: white;
}

.dark-toggle:hover {
  background-color: #4b5563; 
}
