#about {
  max-width: 800px;
  margin: 4rem auto;
  padding: 2rem;
  border-radius: 1rem;
  text-align: center;
  line-height: 1.8;
  transition: background 0.4s ease, color 0.4s ease, box-shadow 0.4s ease;
}

/* Light Mode */
#about {
  background: #ffffff;
  color: #1f2937; /* Gray-800 */
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
}

/* Dark Mode */
.dark #about {
  background: linear-gradient(145deg, #1e293b, #0f172a);
  color: #f1f5f9; /* Gray-100 */
  box-shadow: 0 0 25px rgba(59, 130, 246, 0.3); /* Soft blue glow */
}

/* Heading */
#about h3 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  position: relative;
  display: inline-block;
}

/* Fancy underline */
#about h3::after {
  content: "";
  display: block;
  width: 60%;
  height: 3px;
  margin: 0.5rem auto 0;
  border-radius: 2px;
  background: linear-gradient(90deg, #3b82f6, #8b5cf6);
  transition: width 0.3s ease;
}

#about h3:hover::after {
  width: 100%;
}

/* Paragraph */
#about p {
  font-size: 1.1rem;
  opacity: 0.9;
  padding: 0 1rem;
  animation: fadeIn 1s ease forwards;
}

/* Fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 768px) {
  #about {
    margin: 2rem 1rem;
    padding: 1.5rem;
  }

  #about h3 {
    font-size: 1.6rem;
  }

  #about p {
    font-size: 1rem;
  }
}
