/* Navbar.css */

.navbar {
  width: 100%;
  position:fixed;
  top: 0;
  left: 10;
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  padding: 15px 30px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  transition: background 0.3s ease;
}

.navbar .container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-size: 1rem;

  color: #fff;
  letter-spacing: 1px;
  cursor: default;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 25px;
}

/* Navigation links */
.nav-links a {
  text-decoration: none;
  font-size: 1rem;
  color: #fff;
  font-weight: 500;
  position: relative;
  transition: color 0.3s ease;
}

.nav-links a::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0%;
  height: 2px;
  background: #fff;
  transition: width 0.3s ease;
}

.nav-links a:hover::after {
  width: 100%;
}

.nav-links a:hover {
  color: #ffe259;
}

/* Dark mode toggle button */
.dark-toggle {
  background: transparent;
  border: none;
  font-size: 1.4rem;
  cursor: pointer;
  color: #fff;
  transition: transform 0.2s ease, color 0.3s ease;
}

.dark-toggle:hover {
  transform: scale(1.2);
  color: #ffe259;
}

/* Dark mode styles */
body.dark-mode .navbar {
  background: linear-gradient(135deg, #0f2027, #2c5364);
}

body.dark-mode .nav-links a {
  color: #f1f1f1;
}

body.dark-mode .nav-links a:hover {
  color: #00c6ff;
}
